body{
  font: 18px/24px 'Circe', sans-serif;
  background: #fff;
  color: #202026;
}
body,html{
  height: 100%;
}
.wrapper{
  min-height: calc(100vh - 109px);
  // padding-bottom: 10px;
}
input{outline: none;}
.center{
  width: 100%;
  max-width: 1290px;
  padding: 0 15px;
  margin: 0 auto;
  &:after,&:before{
    content: '';
    display: table;
    clear: both;
  }
}
.btn{
  transition: all 0.3s ease;
  border-radius: 5px;
}
.btn-transparent{
  border: 1px solid #000;
  color: #000;
  background: #fff;
  &:hover{
    background: #000;
    color: #fff;
  }
  &.white{
    border-color: #fff;
    color: #fff;
    background: none;
    &:hover{
      background: #2ac1d9;
    }
  }
}
.btn-white{
  background: #fff;
  color: #0149a1;
  font-size: 17px;
  font-weight: normal;
  border: 1px solid #fff;
  &:hover{
    background: none;
    color: #fff;
  }
}
.title_page{
  margin: 0;
  font-size: 60px;
  line-height: 63px;
  font-weight: bold;
  padding: 23px 0 35px;
  @media screen and (max-width: 991px) {
    font-size: 35px;
    line-height: 38px;
  }
}
.soc_ico{
  margin: 26px 0 0 0;
  padding: 0;
  list-style: none;
  font-size: 0;
  li{
    display: inline-block;
    vertical-align: middle;
    padding-right: 24px;
    &:last-child{padding-right: 0;}
    a{
      display: inline-block;
      vertical-align: middle;
      width: 24px;
      height: 24px;
      position: relative;
    }
    &.vk_ico a{
      background: url("data:image/png;base64,");
    }
    &.fb_ico{
      a{
        background: url("data:image/png;base64,");
      }
    }
    span{
      display: inline-block;
      vertical-align: middle;
      font-size: 18px;
      line-height: 23px;
      padding-left: 8px;
      position: relative;
      top: 2px;
      color: #fff;
    }
  }
}
.list_posts{
  display: flex;
  flex-wrap: wrap;
  .post{
    flex: 0 0 50%;
    max-width: 50%;
  }
  .post-img{
   max-height: 100%;
   display: block;
   width: auto;
   right: 15px !important;
   padding: 20px;
  }
  .big_post{
    flex: 0 0 100%;
    max-width: 100%;
  }
  @media screen and (max-width: 991px) {
    .post{
      flex: 0 0 100%;
      max-width: 100%;
      margin-bottom: 15px;
      h2{
        font-size: 30px !important;
        line-height: 37px !important;
      }
    }
  }
}
.last_post,.post_title{
  padding: 0 15px;
  background: rgb(0,93,178);
  background: -moz-linear-gradient(left, rgba(0,93,178,1) 0%, rgba(0,96,179,1) 6%, rgba(0,109,185,1) 18%, rgba(0,167,208,1) 55%, rgba(0,181,213,1) 65%, rgba(0,191,218,1) 77%, rgba(0,191,218,1) 100%);
  background: -webkit-linear-gradient(left, rgba(0,93,178,1) 0%,rgba(0,96,179,1) 6%,rgba(0,109,185,1) 18%,rgba(0,167,208,1) 55%,rgba(0,181,213,1) 65%,rgba(0,191,218,1) 77%,rgba(0,191,218,1) 100%);
  background: linear-gradient(to right, rgba(0,93,178,1) 0%,rgba(0,96,179,1) 6%,rgba(0,109,185,1) 18%,rgba(0,167,208,1) 55%,rgba(0,181,213,1) 65%,rgba(0,191,218,1) 77%,rgba(0,191,218,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#005db2', endColorstr='#00bfda',GradientType=1 );
  .center{position: relative;}
  .post_bg{
    background: none !important;
    min-height: auto !important;
    display: block !important;
  }
  [class*="col-"]{position: static;}
  .big_post{
    padding: 42px 0 0 0;
    h2{
      font-size: 38px !important;
      line-height: 48px !important;
    }
  }
  .post_cnt{
    padding: 15px !important;
  }
  @media screen and (max-width: 991px) {
    padding: 0;
    .big_post{
      h2{
        font-size: 30px !important;
        line-height: 37px !important;
      }
    }
  }
}
.load_more{
  margin: 20px 0 50px;
  .btn{
    max-width: 175px;
    margin: 0 auto;
    font-size: 17px;
    height: 54px;
    line-height: 40px;
  }
  @media screen and (max-width: 767px){
    margin-bottom: 30px;
  }
}
.list_posts{
  .post{
    &:nth-child(4n+1){
      .post_bg{
        background: #005dad;
      }
    }
    &:nth-child(4n+2){
      .post_bg{
        background: #0194c7;
      }
    }
    &:nth-child(4n+3){
      .post_bg{
        background: #00bed8;
      }
    }
    &:nth-child(4n+4){
      .post_bg{
        background: #0079ba;
      }
    }
  }
}
.icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
}

/* ==========================================
Single-colored icons can be modified like so:
.icon-name {
  font-size: 32px;
  color: red;
}
========================================== */

.icon-logo {
  width: 2.623046875em;
}
.list_icon{
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 0;
  position: absolute;
  left: 15px;
  top: -17px;
  z-index: 10;
  li{
    width: 34px;
    height: 34px;
    background: #005dae;
    display: inline-block;
    vertical-align: middle;
    border: 2px solid #fff;
    border-radius: 50%;
    text-align: center;
    margin-right: 17px;
    font-size: 19px;
    position: relative;
    .icon{
      position: absolute;
      top: 50%;
      left: 50%;
      transform:translate(-50%,-50%);
      &.icon-transport{
        transform:translate(-50%,-50%) rotate(45deg);
      }
    }
  }
}
.subscribe{
  padding-bottom: 36px;
  position: relative;
  z-index: 2;
  h3{
    margin: 0;
    font-size: 22px;
    line-height: 24px;
    font-weight: bold;
    color: #fff;
    padding-bottom: 20px;
  }
  p{
    margin: 12px 0 0 0;
    font-size: 16px;
    line-height: 22px;
    color: #fff;
    font-weight: 300;
    a{
      color: #fff;
      text-decoration: underline;
      &:hover{
        text-decoration: none;
      }
    }
  }
  .subscribe_row{
    display: flex;
    input{
      height: 52px;
      margin: 0 20px 0 0;
      padding: 0 15px;
      font-size: 17px;
      font-weight: 300;
      border: 1px solid #fff;
      border-radius: 5px;
      background: none;
      color: #fff;
      flex: 1 1 100%;
      &::-webkit-input-placeholder {color:#fff;opacity: 1;}
      &::-moz-placeholder          {color:#fff;opacity: 1;}
      &:-moz-placeholder           {color:#fff;opacity: 1;}
      &:-ms-input-placeholder      {color:#fff;opacity: 1;}
    }
    .btn{
      flex: 0 0 172px;
      max-width: 172px;
    }
  }
  
  @media screen and (max-width: 767px) {
    .subscribe_row {
      input{
        min-width: 0;
      }
      .btn{
        flex:none;
      }
    }
  }
  @media screen and (max-width: 420px){
    .subscribe_row{
      display: block;
      input{
        display: block;
        width: 100%;
        margin: 0;
      }
      .btn{
        width: 100%;
        max-width: none !important;
        margin-top: 10px;
      }
    }
  }
}
.meta_data{
    font-size: 16px;
    line-height: 20px;
    margin-top: 10px;
    em{
      font-style: normal;
      padding-right: 6px;
      position: relative;
      display: inline-block;
      vertical-align: middle;
      &:before{
          content: '';
          width: 2px;
          height: 14px;
          display: block;
          position: absolute;
          top: 2px;
          right: -6px !important;
          background: #fff;
        }
    }
    a{
      color: #fff;
    }
  }
.post_container_img{
    position: absolute;
    top: 50%;
    right: 0;
    height: 100%;
   
    width: 70%;
    overflow: hidden;
    transform:translateY(-50%);
    img{
      display: block;
      max-width: 100%;
      max-height: 100%;
      position: absolute;
      top: 50%;
      right: 0;
      transform:translateY(-50%);
    }
    @media screen and (max-width: 991px){
      opacity: .3;
      height: 100%;
       height: calc(100% - 20px);
      width: 100%;
      img{
        max-height: 100%;
        width: auto;
      }
    }
}
.last_post{
  .post_container_img{right: 0 !important;}
}
.post{
  .post_container_img{
    right: 30px;
    opacity: .3;
    width: calc(100% - 60px);
  }
  &.big_post{
    .post_container_img{
      opacity: 1;
      @media screen and (max-width: 991px){
        opacity: .3;
      }
    }

  }
}
.post_title{
  color: #fff;
  height: 448px;
  display: flex;
  overflow: hidden;
  h1{
    margin: 0;
    font-size: 58px;
    line-height: 69px;
    font-weight: bold;
  }

  @media screen and (max-width: 991px) {
    height: 250px;
    
    .post_title_cnt h1{
      width: 100%;
    }
  }
  @media screen and (max-width: 767px){
    h1{
      font-size: 30px;
      line-height: 36px;
    }
    .post_title_cnt{
      padding-top: 15px !important;
    }
    .post_title_footer{
      display: block !important;
      padding-bottom: 15px !important;
      .meta_tag{
        display: block !important;
        a:first-of-type{
          margin-left: 0;
        }
      }
    }
  }
  @media screen and (max-width: 520px){
    height: auto;
    min-height: 200px;
  }
}
.meta_tag,.meta_data{
  display: block;
  font-size: 16px;
  line-height: 20px;
  font-weight: 300;
  color: #fff;
  em{font-style: normal; padding-right: 5px;}
  a{
    display: inline-block;
    vertical-align: middle;
    color: #fff;

    margin: 0 10px;
    + a{
      position: relative;
      &:before{
        content: '';
        width: 2px;
        height: 14px;
        display: block;
        position: absolute;
        top: 2px;
        left: -10px;
        background: #fff;
      }
    }
  }
}
.post_title_cnt{
  height: 100%;
  padding-top: 41px;
  display: flex;
  flex-flow:column nowrap;
  position: relative;
  z-index: 2;
  h1{
    width: 50%;
  }
  .post_title_footer{
    margin-top: auto;
    padding-bottom: 30px;
    .meta_data,.meta_tag{
      display: block;
      margin-top: 0;
    }
    .meta_tag{
     margin-top: 10px;
     a{
      &:first-of-type{
        margin-left: 0;
      }
     }
    }
  }
}
.centered{
  margin: 24px auto 0;
  max-width: 75%;
  @media screen and (max-width: 991px) {
    max-width: none;
  }
}
.breadcrumbs{
  margin: 20px 0 0 0;
  padding: 0;
  list-style: none;
  font-size: 0;
  li{
    display: inline-block;
    vertical-align: middle;
    font-size: 16px;
    line-height: 20px;
    color: #0fc2da;
    padding-right: 26px;
    position: relative;
    margin-bottom: 5px;
    &:after{
      content: '';
      width: 2px;
      height: 14px;
      position: absolute;
      top: 3px;
      right: 13px;
      background: #0fc2da;
    }
    &:last-child{
      padding-right: 0;
      &:after{content: none;}
    }
    a{
      color: #000;
      transition: all 0.3s ease;
      &:hover{
        color: #0fc2da;
        text-decoration: none;
      }
    }
  }
  @media screen and (max-width: 767px){
    li{
      font-weight: 300;
    }
  }
}
.post_content{
  p{
    margin: 0;
    font-size: 20px;
    line-height: 28px;
    font-weight: 300;
    padding-bottom: 27px;
    a{
      color: #2ac1d9;
      text-decoration: underline;
      &:hover{text-decoration: none;}
    }
  }
  h2{
    margin: 0;
    font-size: 36px;
    line-height: 40px;
    color: #000;
    font-weight: bold;
    padding-bottom: 20px;
  }
  h3{
    margin: 0;
    font-size: 26px;
    line-height: 30px;
    font-weight: bold;
    padding-bottom: 10px;
  }
  h4{
    margin: 0;
    font-size: 22px;
    line-height: 26px;
    font-weight: bold;
    padding-bottom: 10px;
  }
  @media screen and (max-width: 767px){
    h2{
      font-size: 28px;
      line-height: 30px;
      padding-bottom: 5px;
    }
    p{
      font-size: 18px;
      line-height: 24px;
      padding-bottom: 10px;
    }
    ul{
      margin: 10px 0;
      li{
        font-size: 18px;
        line-height: 24px;
      }
    }
    h3{
      font-size: 22px;
      line-height: 26px;
      padding-bottom: 5px;
    }
    h4{
      font-size: 20px;
      line-height: 24px;
      padding-bottom: 5px;
    }
  }
}
blockquote{
  margin: 10px 0 37px 0;
  padding: 22px 0 20px 64px;
  border: 0;
  position: relative;
  &:before{
    content: '';
    height: 100%;
    width: 7px;
    position: absolute;
    top: 0;
    left: 0;
    background: rgb(0,191,218);
    background: -moz-linear-gradient(top, rgba(0,191,218,1) 0%, rgba(0,175,212,1) 22%, rgba(0,109,185,1) 79%, rgba(0,93,178,1) 100%);
    background: -webkit-linear-gradient(top, rgba(0,191,218,1) 0%,rgba(0,175,212,1) 22%,rgba(0,109,185,1) 79%,rgba(0,93,178,1) 100%);
    background: linear-gradient(to bottom, rgba(0,191,218,1) 0%,rgba(0,175,212,1) 22%,rgba(0,109,185,1) 79%,rgba(0,93,178,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00bfda', endColorstr='#005db2',GradientType=0 );
  }
  q{
    margin: 0;
    padding: 0;
    font-size: 20px;
    line-height: 28px;
  }
  @media screen and (max-width: 767px){
    margin-bottom: 20px;
    padding:5px 0 5px  17px;
    q{
      font-size: 18px;
      line-height: 24px;
    }
  }
}
.marked_list{
  margin: 20px 0 30px 0;
  padding: 0;
  list-style: none;
  li{
    position: relative;
    padding-left: 35px;
    font-size: 20px;
    line-height: 28px;
    font-weight: 300;
    padding-bottom: 7px;
    &:before{
      content: '';
      background: #2ac1d9;
      width: 10px;
      height: 10px;
      border-radius: 50%;
      position: absolute;
      top: 9px;
      left: 0;
    }
  }
 @media screen and (max-width: 767px) {
  li{
    padding-left: 15px;
    &:before{
      top: 7px;
    }
  }
 }
}
.post_subscribe{
  background: #2ac1d9;
  border-radius: 5px;
  padding:20px 15px 40px;
  margin-bottom: 40px;
  margin-top: 30px;
  text-align: center;
  .subscribe{
    padding-bottom: 0;
  }
  .subscribe_form{
    max-width: 70%;
    margin: 0 auto;
  }
  p{
    font-size: 16px !important;
    line-height: 20px !important;
    margin: 14px 0 0 0 !important;
    padding: 0 !important;
  }
  a{
    color: #fff !important;
  }
  h3{
    padding: 0 0 20px 0 !important;
    font-size: 22px !important;
    line-height: 24px !important;
  }
  .btn{
    color: #000;
    &:hover{
      color: #fff;
    }
  }
  @media screen and (max-width: 767px) {
    .subscribe_form{
      max-width: none;
    }
  }
}
.share_block{
  border-radius: 5px;
  padding: 20px 15px;
  text-align: center;
  background: #005dad;
  color: #fff;
  .soc_ico{
    margin: 10px 20px;
    display: inline-block;
    vertical-align: middle;
    li{
      padding-right: 10px;
    }
  }
  h3{
    margin: 0 !important;
    font-size: 22px !important;
    line-height: 24px !important;
    padding: 0 !important;
    display: inline-block;
    vertical-align: middle;
  }
}
.read_more_block{
  margin-top: 15px;
  padding-bottom: 20px;
  a{
    &:hover{
      text-decoration: none;
      img{
        transition: all .7s ease !important;
        transform:translate(-50%,-50%) scale(1.1, 1.1) !important;
      }
    }
  }
  h3{
    margin: 0;
    font-size: 26px !important;
    line-height: 30px !important;
    padding: 0 0 16px 0 !important;
  }
  [class*="col-"]{
    margin-bottom: 20px;
    .post_pr{
      background: #0095c6;
      display: block;
      height: 275px;
      overflow: hidden;
      padding: 30px;
      em{
        display: block;
        width: 100%;
        min-height: 100%;
        position: relative;
      }
      img{
        position: absolute;
        max-width: 100%;
        max-height: 100%;
        display: block;
        top: 50%;
        left: 50%;
        transition: all .3s ease;
        transform:translate(-50%,-50%);
      }
    }
    + [class*="col-"]{
      .post_pr{
        background: #21b3e3;
      }
    }
    strong{
      display: block;
      font-size: 22px !important;
      line-height: 30px !important;
      font-weight: bold;
      color: #000;
      margin-top: 11px;
    }
  }
  @media screen and (max-width: 767px) {
    [class*="col-"] .post_pr{
      height: 200px;
    }
    h3{
      font-size: 24px !important;
      line-height: 26px !important;
      padding-bottom: 10px !important;
    }
    [class*="col-"]{
      margin-bottom: 15px;
      strong{
        font-size: 18px !important;
        margin-top: 0;
      }
      
    }
  }
}
.example{
  width: 100%;
  display: block;
  height: auto;
}
.comment_block{
  margin-top: 50px;
}